import React from "react";
import {Badge, Button, Offcanvas} from "react-bootstrap";
import {Phone, Whatsapp} from "react-bootstrap-icons";
 const MoreInfo =(props)=>{
     const darkM = props.darkM

     return (
         <div>
             <Offcanvas show={props.show} onHide={props.handleClose} placement='end'
                        style={{color:(props.darkM?'#000000':'#ffffff'),backgroundColor:(!props.darkM?'#1a1e23':'#ffffff')}}
             >
                 <Offcanvas.Header closeButton>
                     <Offcanvas.Title>Больше информации</Offcanvas.Title>
                 </Offcanvas.Header>
                 <Offcanvas.Body >
                  <h2 >{props.value.name}</h2>
                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     {props.value.phone ?<>
                     <Button variant='primary' type='tel'
                             href={'tel:' + props.value.phone}><Phone/> {props.value.phone} </Button>
                     <Button className='m-3' variant='success' type='tel' href={'https://wa.me/'+props.value.phone.replace(/[^0-9]/g, '')} target="_blank">
                         <Whatsapp/>
                     </Button>
                         </> :null}
                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     {props.value.address ? <Button variant={!darkM?'warning':'primary'} href={'https://yandex.ru/maps/?text=' + props.value.address} bg="secondary" target="_blank">{props.value.address}</Button>
                    :null }
                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     Рейтинг среди ресторанов : {props.value.ranking}
                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     <div>Описание заведения:</div>
                     <div>{props.value.description || null}</div>

                 </Offcanvas.Body>
             </Offcanvas>
         </div>
     )
 }

 export default MoreInfo